.list {
  display: flex;
  list-style: none;
  margin-bottom: 15px;
}

.item {
  &:not(:last-child) {
    margin-right: 10px;
  }
}

.button {
  padding: 5px 10px;
  border: 1px solid #d8d3d3;
  border-radius: 10px;

  background-color: #fff;

  font-weight: 500;
  text-transform: capitalize;

  transition: all 250ms linear;

  cursor: pointer;

  &:hover,
  &:focus {
    background-color: #d8d3d3;
    color: #6b6b6b;
    transition: all 250ms linear;
  }
}
